<template>
  <img
    class="avatar-img Avatar-container"
    :src="url"
    :style="{ width: size + 'px', height: size + 'px' }"
  />
</template>

<script>
export default {
  props: {
    //   属性约束
    url: {
      type: String,
      //   属性类型：字符串
      required: true,
      //   必填
    },
    size: {
      type: Number,
      default: 150,
    },
  },
};
</script>
<style scoped>
.avatar-img {
  border-radius: 50%;
  object-fit: contain;
  display: block;
}
</style>
