<template>
  <form
    id="talk"
    @submit.prevent="handlesubmit"
    ref="continer"
    class="data-form-container"
  >
    <div class="form-item">
      <div class="input-area">
        <input
          maxlength="10"
          type="text"
          placeholder="用户昵称"
          v-model="formdata.nickname"
        />
        <span class="tip">{{ formdata.nickname.length }}/10</span>
      </div>
      <div class="error">{{ error.nickname }}</div>
    </div>
    <div class="form-item">
      <div class="text-area">
        <textarea
          maxlength="300"
          placeholder="输入内容"
          v-model="formdata.nicktent"
        ></textarea>
        <span class="tip">{{ formdata.nicktent.length }}/300</span>
      </div>
      <div class="error">{{ error.nicktent }}</div>
      <div class="form-item">
        <div class="button-area">
          <button :disabled="isSubmit">
            {{ isSubmit ? "提交中..." : "提交" }}
          </button>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      formdata: {
        // 表单数据
        nickname: "",
        nicktent: "",
      },
      error: {
        // 错误数据
        nickname: "",
        nicktent: "",
      },
      isSubmit: false,
    };
  },
  methods: {
    handlesubmit() {
      this.error.nickname = this.formdata.nickname ? "" : "请填写账号";
      this.error.nicktent = this.formdata.nicktent ? "" : "请填写内容";
      if (this.error.nickname || this.error.nicktent) {
        return;
      }
      this.isSubmit = true;
      this.$emit("submit", this.formdata, (successMess) => {
        this.$showMessage({
          content: successMess,
          type: "success",
          duration: 2000,
          container: this.$refs.continer,
          callback: () => {
            this.isSubmit = false;
            this.formdata.nickname = "";
            this.formdata.nicktent = "";
          },
        });
      });
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/css/color.less";
.data-form-container {
  margin-bottom: 20px;
  overflow: hidden;
}
.form-item {
  margin-bottom: 8px;
}
.input-area {
  width: 50%;
  position: relative;
}
.text-area {
  position: relative;
}
.tip {
  position: absolute;
  right: 5px;
  bottom: 5px;
  color: #b4b8bc;
  font-size: 12px;
}
input,
textarea {
  display: block;
  width: 100%;
  box-sizing: border-box;
  border: 1px dashed @gray;
  outline: none;
  color: @words;
  font-size: 14px;
  border-radius: 4px;
  &:focus {
    border-color: @primary;
  }
}
input {
  padding: 0 15px;
  height: 40px;
}
textarea {
  resize: none;
  padding: 8px 15px;
  height: 120px;
}
.error {
  margin-top: 6px;
  color: @danger;
  font-size: 14px;
  height: 20px;
  line-height: 20px;
}
button {
  position: relative;
  cursor: pointer;
  border: none;
  outline: none;
  width: 100px;
  height: 34px;
  color: #fff;
  border-radius: 4px;
  background: @primary;
  &:hover {
    background: darken(@primary, 10%);
  }
  &:disabled {
    background: lighten(@primary, 20%);
    cursor: not-allowed;
  }
}
</style>
