export default function (time, istime = false) {
  const data = new Date(+time);
  const month = (data.getMonth() + 1).toString().padStart(2, "0");
  const day = (data.getDate() + 1).toString().padStart(2, "0");
  let str = `${data.getFullYear()}-${month}-${day}`;
  if (istime) {
    const hour = data.getHours().toString().padStart(2, "0");
    const minice = data.getMinutes().toString().padStart(2, "0");
    const secends = data.getSeconds().toString().padStart(2, "0");
    str += ` ${hour}-${minice}-${secends}`;
  }
  return str;
}
