<template>
  <div @click="handleclick" v-show="show" class="to-totop-container">Top</div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
    };
  },
  methods: {
    handleScroll(dom) {
      if (!dom) {
        this.show = false;
        return;
      }
      this.show = dom.scrollTop >= 500;
    },
    handleclick() {
      this.$bus.$emit("setscroll", 0);
    },
  },
  created() {
    this.$bus.$on("mainScroll", this.handleScroll);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.handleScroll);
  },
};
</script>

<style scoped lang="less">
@import "~@/css/color.less";
.to-totop-container {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: @primary;
  position: fixed;
  right: 250px;
  bottom: 100px;
  z-index: 999;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
}
</style>
