import eventBus from "@/eventBus";
import Gif from "@/asset/default.gif";
eventBus.$on("mainScroll", function (dom) {
  setImages();
});
let imgs = [];
/**
 * 调用该函数则会显示希望现实的图片
 */

function setImage(img) {
  img.dom.src = Gif;
  const dheight = document.documentElement.clientHeight;
  const rect = img.dom.getBoundingClientRect();
  const height = rect.height || 100;
  if (rect.top >= -height && rect.top <= dheight) {
    const temImg = new Image();
    temImg.onload = function () {
      img.dom.src = img.src;
    };
    temImg.src = img.src;
    imgs = imgs.filter((i) => i !== img);
  }
}

function setImages() {
  for (const img of imgs) {
    //   处理单张图片
    setImage(img);
  }
}

export default {
  inserted(el, bindings) {
    const img = {
      dom: el,
      src: bindings.value,
    };
    imgs.push(img);
    // 立即处理
    setImage(img);
  },
  unbind(el) {
    imgs = imgs.filter((img) => img.dom != el);
  },
};
