<template>
  <div class="message-area-container">
    <Datafrom v-on="$listeners" />
    <h3>{{ title }}</h3>
    <span>{{ subTitle }}</span>
    <DataList :list="list" />
    <div class="loading" v-vloading="isListLoading"></div>
  </div>
</template>

<script>
import Datafrom from "./DataFrom";
import DataList from "./Datalist";
export default {
  components: {
    Datafrom,
    DataList,
  },
  props: {
    title: {
      type: String,
      default: "",
    },
    subTitle: {
      type: String,
      default: "",
    },
    list: {
      type: Array,
      default: () => [],
    },
    isListLoading: {
      type: Boolean,
      default: false,
    },
  },
};
</script>

<style scoped lang="less">
.loading {
  position: relative;
  height: 100px;
}
</style>
