// 公共获取远程数据代码

// 组件中需要一个方法提供获取远程数据的方法,这里只需要调用
export default function (defaultDataValue = null) {
  //defaultDataValue 为请求到的数据格式为对象或者数组
  return {
    data() {
      return {
        // 是否正在获取远程数据
        isLoading: true,
        // 获取的远程数据
        data: defaultDataValue,
      };
    },
    async created() {
      this.data = await this.fetchData();
      this.isLoading = false;
    },
  };
}
