import { getsetting } from "@/api/setting";
export default {
  namespaced: true,
  state: {
    loading: false,
    data: [],
  },
  mutations: {
    setLoading(state, payload) {
      state.loading = payload;
    },
    setData(state, payload) {
      state.data = payload;
    },
  },
  actions: {
    async fetchsetting(ctx) {
      ctx.commit("setLoading", true);
      const resp = await getsetting();
      ctx.commit("setData", resp);
      ctx.commit("setLoading", false);
      if (resp.favicon) {
        let link = document.querySelector("link[ref='sortcut icon]");
        if (link) {
          return;
        }
        const lic = document.createElement("link");
        lic.rel = "shortcut icon";
        lic.type = "images/x-icon";
        lic.href = resp.favicon;
        document.querySelector("head").append(lic);
      }
    },
  },
};
