<template>
  <div class="blog-comment-container">
    <MessageArea
      title="评论列表"
      :subTitle="`(${data.total})`"
      :list="data.rows"
      :isListLoading="isLoading"
      @submit="handlesubmit"
    />
  </div>
</template>

<script>
import MessageArea from "@/comopnents/MessageArea";
import fetchData from "@/mixins/fetchData";
import { getComments, postComment } from "@/api/blog";
export default {
  components: {
    MessageArea,
  },
  mixins: [fetchData({ total: 0, rows: [] })],
  computed: {
    isjiazai() {
      return this.data.rows.length > this.data.total;
    },
  },
  methods: {
    async fetchData() {
      return await getComments(this.$route.id, this.page, this.limit);
    },

    // 加载更多数据
    async formmath() {
      if (this.isjiazai) {
        return;
      }
      this.isLoading = true;
      this.page++;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
    async handlesubmit(formdata, callback) {
      const resp = await postComment({
        blogId: this.$route.params.id,
        ...formdata,
      });
      this.data.rows.unshift(resp);
      this.data.total++;
      callback("评论成功");
      // 处理完了
    },

    // 处理滚动,滚动到底时触发加载更多评论
    handleScroll(dom) {
      const mastheight = 100;
      const jiazai = Math.abs(
        dom.scrollTop + dom.clientHeight - dom.scrollHeight
      );
      if (jiazai <= mastheight && !this.isLoading && dom) {
        this.formmath();
      }
    },
  },
  created() {
    this.$bus.$on("mainScroll", this.handleScroll);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.handleScroll);
  },
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
};
</script>

<style scoped lang="less">
.blog-comment-container {
  margin: 30px 0;
}
</style>
