<template>
  <div
    class="Home-container"
    ref="container"
    @wheel="handleWheel"
    v-vloading="loading"
  >
    <ul
      class="carusel-container"
      :style="{ marginTop: margintop }"
      @transitionend="handleTransitonEnd"
    >
      <li v-for="item in data" :key="item.id">
        <Carouselitem :carouse="item" />
      </li>
    </ul>
    <div class="icon arrowUp" v-show="index !== 0" @click="switchTo(index - 1)">
      <Icon type="arrowUp" />
    </div>
    <div
      class="icon arrowDown"
      v-show="index < data.length - 1"
      @click="switchTo(index + 1)"
    >
      <Icon type="arrowDown" />
    </div>
    <ul class="indicator" v-show="data.length !== 1">
      <li
        v-for="(item, i) in data"
        :key="item.id"
        :class="{ active: i === index }"
        @click="switchTo(i)"
      ></li>
    </ul>
  </div>
</template>

<script>
// 发送请求
import { getbanner } from "@/api/banner";
// 首页组件
import Carouselitem from "./Cariyselitem.vue";
// 首页组件中的上下箭头
import Icon from "@/comopnents/Icon";

import { mapState } from "vuex";

// 导入混合组件
export default {
  components: {
    Carouselitem,
    Icon,
  },
  data() {
    return {
      index: 0, //当前显示第几章轮播图
      containerHeight: 0, //容器高度，初始为零
      switching: false, //是否正在翻页
    };
  },
  mounted() {
    this.containerHeight = this.$refs.container.clientHeight;
    window.addEventListener("resize", this.handleResize);
  },
  created() {
    this.$store.dispatch("banner/fetchBanner");
  },
  // 组件销毁移除事件
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },

  computed: {
    margintop() {
      return -this.index * this.containerHeight + "px";
    },
    ...mapState("banner", ["loading", "data"]),
  },
  methods: {
    switchTo(i) {
      this.index = i;
    },
    handleWheel(e) {
      if (this.switching) {
        return;
      }
      if (e.deltaY < -5 && this.index !== 0) {
        // console.log("1");
        // 上滚动
        this.index--;
        this.switching = true;
      } else if (e.deltaY > 5 && this.index !== this.data.length - 1) {
        // 下滚动
        this.index++;
        this.switching = true;
      }
    },
    handleTransitonEnd() {
      this.switching = false;
    },

    // 浏览器尺寸改变处理函数
    handleResize() {
      this.containerHeight = this.$refs.container.clientHeight;
    },
    async fetchData() {
      return await getbanner();
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/css/mixin.less";
@import "~@/css/color.less";

.Home-container {
  width: 100%;
  height: 100%;
  // background: red;
  margin: 0;
  overflow: hidden;
  position: relative;
  .carusel-container {
    list-style: none;
    transition: 500ms;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    li {
      margin: 0;
      padding: 0;
      width: 100%;
      height: 100%;
    }
  }
  .icon {
    .self-center();
    @gap: 25px;
    margin: 0;
    padding: 0;
    font-size: 30px;
    color: @gray;
    cursor: pointer;
    &.arrowUp {
      top: @gap;
      transform: translateX(-50%);
      animation: jump-up 2s infinite;
    }
    &.arrowDown {
      top: auto;
      bottom: @gap;
      transform: translateX(-50%);
      animation: jump-down 2s infinite;
    }
    @jump: 5px;
    @keyframes jump-up {
      0% {
        transform: translate(-50%, @jump);
      }
      50% {
        transform: translate(-50%, -@jump);
      }
      100% {
        transform: translate(-50%, @jump);
      }
    }
    @keyframes jump-down {
      0% {
        transform: translate(-50%, -@jump);
      }
      50% {
        transform: translate(-50%, @jump);
      }
      100% {
        transform: translate(-50%, -@jump);
      }
    }
  }
  .indicator {
    .self-center();
    transform: translateY(-50%);
    right: 15px;
    left: auto;
    list-style: none;
    padding: 0;
    li {
      width: 7px;
      height: 7px;
      background: @words;
      cursor: pointer;
      border-radius: 50%;
      margin-bottom: 10px;
      border: 1px solid #fff;
      box-sizing: border-box;
      // transition:
      &.active {
        background: #fff;
      }
    }
  }
}
</style>
