<template>
  <i class="iconfont Icon-container" :class="fontClass"></i>
</template>

<script>
const classMap = {
  home: "icon-shouye",
  success: "icon-zhengque",
  error: "icon-cuowu",
  close: "icon-guanbi",
  warn: "icon-jinggao",
  info: "icon-about",
  blog: "icon-blog",
  code: "icon-code",
  about: "icon-set_about_hov",
  weixin: "icon-weixin",
  mail: "icon-email",
  github: "icon-github-fill",
  qq: "icon-QQ",
  arrowUp: "icon-shangjiantou-copy",
  arrowDown: "icon-xiajiantou-copy",
  empty: "icon-empty",
  chat: "icon-liuyan",
};
export const types = Object.keys(classMap);
export default {
  props: {
    type: {
      type: String,
      required: true,
    },
  },
  computed: {
    //   利用计算样式形成映射便于语义化
    fontClass() {
      return classMap[this.type];
    },
  },
};
</script>

<style scoped>
@import "//at.alicdn.com/t/font_3167129_urcm0p0kk8a.css";
.iconfont {
  color: inherit;
  font-size: inherit;
}
</style>
