<template>
  <div class="pager-container" v-if="pageNumber > 1">
    <a :class="{ disabled: current === 1 }" @click="handleClick(1)"
      >.|&lt;&lt;</a
    >
    <a :class="{ disabled: current === 1 }" @click="handleClick(current - 1)"
      >&lt;&lt;</a
    >
    <a
      :class="{ active: current === item }"
      v-for="(item, i) in numbers"
      :key="i"
      @click="handleClick(item)"
      >{{ item }}</a
    >
    <a
      :class="{ disabled: current === pageNumber }"
      @click="handleClick(current + 1)"
      >&gt;&gt;</a
    >
    <a
      :class="{ disabled: current === pageNumber }"
      @click="handleClick(pageNumber)"
      >&gt;&gt;|</a
    >
  </div>
</template>

<script>
export default {
  props: {
    current: {
      // 当前页
      type: Number,
      default: 1,
    },
    total: {
      // 数据量
      type: Number,
      default: 0,
    },
    limit: {
      // 页容量
      type: Number,
      default: 10,
    },
    visibleNumber: {
      type: Number,
      default: 10,
    },
  },
  computed: {
    pageNumber() {
      return Math.ceil(this.total / this.limit);
    },
    minPage() {
      var minPage = this.current - this.limit / 2;
      if (minPage < 1) {
        minPage = 1;
      }
      return minPage;
    },
    maxPage() {
      let maxPage = this.minPage + this.limit - 1;
      if (maxPage > this.pageNumber) {
        maxPage = this.pageNumber;
      }
      return maxPage;
    },
    numbers() {
      let numbers = [];
      for (let i = this.minPage; i <= this.maxPage; i++) {
        numbers.push(i);
      }
      return numbers;
    },
  },
  methods: {
    //   组件通信，抛出事件
    handleClick(newpage) {
      //   console.log(e);
      if (newpage < 1) {
        newpage = 1;
      }
      if (newpage > this.pageNumber) {
        newpage = this.pageNumber;
      }
      if (newpage === this.current) {
        return;
      }
      this.$emit("pageChange", newpage);
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/css/color.less";
.pager-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  a {
    color: @primary;
    margin: 0 5px;
    cursor: pointer;
    &:hover {
      color: @words;
    }
    &.disabled {
      color: @lightWords;
      cursor: not-allowed;
    }
    &.active {
      color: @dark;
      font-weight: bold;
      cursor: text;
    }
  }
}
</style>
