<template>
  <div class="blog-list-container" v-vloading="isLoading" ref="scollcontainer">
    <ul>
      <li v-for="item in data.rows" :key="item.id">
        <div class="thumb" v-if="item.thumb">
          <RouterLink :to="{ name: 'Detail', params: { id: item.id } }">
            <img v-lazy="item.thumb" :alt="item.title" :title="item.title" />
          </RouterLink>
        </div>
        <div class="main">
          <RouterLink :to="{ name: 'Detail', params: { id: item.id } }">
            <h2>{{ item.title }}</h2>
          </RouterLink>
          <div class="aside">
            <span>日期：{{ formatData(item.createDate) }}</span>
            <span>浏览：{{ item.scanNumber }}</span>
            <span>评论：{{ item.commentNumber }}</span>
            <a href="/article/cate/8">{{ item.category.name }}</a>
          </div>
          <div class="desc">{{ item.description }}</div>
        </div>
      </li>
    </ul>
    <Pager
      v-if="data.total"
      :current="routeInfo.page"
      :total="data.total"
      :limit="routeInfo.limit"
      @pageChange="handlejump"
    />
  </div>
</template>

<script>
import Pager from "@/comopnents/Pager";
import fetchData from "@/mixins/fetchData";
import { getBlogs } from "@/api/blog";
import { formatData } from "@/utils";
import mainScroll from "@/mixins/mainScroll.js";
export default {
  mixins: [fetchData({}), mainScroll("scollcontainer")],
  components: {
    Pager,
  },
  methods: {
    formatData,
    setscroll(scrolltop) {
      this.$refs.scollcontainer.scrollTop = scrolltop;
    },
    async fetchData() {
      return await getBlogs(
        this.routeInfo.page,
        this.routeInfo.limit,
        this.routeInfo.createid
      );
    },
    handlejump(newpage) {
      const query = {
        page: newpage,
        limit: this.routeInfo.limit,
      };
      // this.routeInfo.page = newpage;
      if (this.routeInfo.createid == -1) {
        // article?page=${newpage}&limit=${this.routeInfo.limit}
        this.$router.push({
          // 命名路由
          name: "blog",
          query,
        });
      } else {
        // article/cate/${this.routeInfo.createid}?page=${newpage}&limit=${this.routeInfo.limit}
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: this.routeInfo.createid,
          },
        });
      }
    },
  },
  computed: {
    // 路由信息
    routeInfo() {
      // 分类id
      const createid = +this.$route.params.categoryId || -1;
      const page = +this.$route.query.page || 1;
      const limit = +this.$route.query.limit || 10;
      return {
        createid,
        page,
        limit,
      };
    },
  },
  watch: {
    // 监控地址变化,地址变化从新请求数据
    $route: {
      async handler() {
        this.isLoading = true;
        this.$refs.scollcontainer.scrollTop = 0;
        this.data = await this.fetchData();
        this.isLoading = false;
      },
    },
  },
  created() {
    this.$bus.$on("setscroll", this.setscroll);
  },
};
</script>

<style scoped lang="less">
@import "~@/css/color.less";
.blog-list-container {
  line-height: 1.7;
  position: relative;
  padding: 20px;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  box-sizing: border-box;
  scroll-behavior: smooth;
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}
li {
  display: flex;
  padding: 15px 0;
  border-bottom: 1px solid @gray;
  .thumb {
    flex: 0 0 auto;
    margin-right: 15px;
    img {
      display: block;
      max-width: 200px;
      border-radius: 5px;
    }
  }
  .main {
    flex: 1 1 auto;
    h2 {
      margin: 0;
    }
  }
  .aside {
    font-size: 12px;
    color: @gray;
    span {
      margin-right: 15px;
    }
  }
  .desc {
    margin: 15px 0;
    font-size: 14px;
  }
}
</style>
