<template>
  <div class="nav-container">
    <h2>文章目录</h2>
    <Right :list="toWithSelect" @select="handleSelect" />
  </div>
</template>

<script>
import Right from "@/views/Blog/compnents/right";
import { debounce } from "@/utils";
export default {
  props: {
    Toc: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      activeAnchor: "",
    };
  },
  components: {
    Right,
  },
  methods: {
    handleSelect(item) {
      location.hash = item.anchor;
    },
    setSleted(dom) {
      if (dom) {
        return;
      }
      this.activeAnchor = "";
      const renge = 100;
      for (const item of this.doms) {
        // dom元素是否选中
        if (!item) {
          continue;
        }
        const top = item.getBoundingClientRect().top;
        if (top >= 0 && top <= renge) {
          this.activeAnchor = item.id;
          return;
        } else if (top > renge) {
          return;
        } else {
          this.activeAnchor = item.id;
        }
      }
    },
  },
  mounted() {
    this.setxuanzhong = debounce(this.setSleted, 50);
    this.$bus.$on("mainScroll", this.setxuanzhong);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.setxuanzhong);
  },
  computed: {
    toWithSelect() {
      const getToc = (toc = []) => {
        return toc.map((t) => ({
          ...t,
          isSleted: t.anchor == this.activeAnchor,
          children: getToc(t.children),
        }));
      };
      return getToc(this.Toc);
    },
    doms() {
      // 根据toc 获取对应的元素数组
      const doms = [];
      const addDoms = (toc) => {
        for (const item of toc) {
          doms.push(document.getElementById(item.anchor));
          if (item.children && item.children.length) {
            addDoms(item.children);
          }
        }
      };
      addDoms(this.Toc);
      return doms;
    },
  },
};
</script>

<style scoped lang="less">
.nav-container {
  width: 100%;
  height: 100%;
  padding: 20px;
}
</style>
