<template>
  <ul class="rightlist-container">
    <li v-for="(item, i) in list" :key="i">
      <span :class="{ active: item.isSleted }" @click="handleClick(item)">{{
        item.name
      }}</span>
      <span
        v-if="item.aside"
        class="math"
        :class="{ active: item.isSleted }"
        @click="handleClick(item)"
        >{{ item.articleCount }}</span
      >
      <RightList :list="item.children" @select="handleClick" />
    </li>
  </ul>
</template>

<script>
export default {
  name: "RightList",
  props: {
    //   每一项内容
    // [{name:xxx,isSleted:true,child:{name:xxx,isSleted:true,child:{name:xxx,isSleted:true,}}}]
    list: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    handleClick(item) {
      this.$emit("select", item);
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/css/color.less";
.rightlist-container {
  list-style: none;
  padding: 0;
  .rightlist-container {
    margin-left: 1em;
  }
  li {
    min-height: 40px;
    line-height: 40px;
    cursor: pointer;
    .active {
      color: @warn;
    }
  }
  .math {
    margin-left: 20px;
  }
}
</style>
