<template>
  <div class="blogCate-container" v-vloading="isLoading">
    <h2>文章分类</h2>
    <Right :list="list" @select="handleSelect" />
  </div>
</template>

<script>
import Right from "../right";
import fetchData from "@/mixins/fetchData";
import { getBlogTypes } from "@/api/blog";
export default {
  mixins: [fetchData([])],
  components: {
    Right,
  },
  created() {
    console.log(this.data);
  },
  computed: {
    categroyId() {
      return +this.$route.params.categoryId || -1;
    },

    // 添加isSelct确定激活状态
    list() {
      const totleArtice = this.data.reduce((a, b) => a + b.articleCount, 0);
      // console.log("2r3qf", totleArtice);
      const resut = [
        { name: "全部", id: -1, articleCount: totleArtice },
        ...this.data,
      ];
      return resut.map((item) => ({
        ...item,
        isSleted: item.id === this.categroyId,
        aside: `${item.articleCount}篇`,
      }));
    },
  },
  methods: {
    async fetchData() {
      return await getBlogTypes();
    },
    handleSelect(item) {
      // 改变路由
      const query = {
        page: 1,
        limit: +this.$route.query.limit || 10,
      };
      if (item.id == -1) {
        this.$router.push({
          name: "blog",
        });
      } else {
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: item.id,
          },
        });
      }
    },
  },
};
</script>

<style scoped lang="less">
.blogCate-container {
  width: 200px;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
  height: 100%;
  overflow-y: scroll;
  h2 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0;
  }
}
</style>
