function isloadingIMG(el) {
  return el.querySelector("img[data-role = loading]");
}

import style from "./loading.module.less";

import loadingUrl from "@/asset/loading.svg";
function createrLoading() {
  const img = document.createElement("img");
  img.dataset.role = "loading";
  img.src = loadingUrl;
  img.className = style.loading;
  return img;
}

// 导出指令配置对象
export default function (el, binding) {
  // 根据binding.value判断创建、删除img元素
  const curimg = isloadingIMG(el);
  if (binding.value) {
    if (!curimg) {
      const img = createrLoading();
      el.appendChild(img);
    }
  } else {
    if (curimg) {
      curimg.remove();
    }
  }
}
