<template>
  <div class="about-container" v-vloading="loading">
    <iframe
      v-if="src"
      class="about-content"
      :src="src"
      frameborder="0"
    ></iframe>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: mapState("about", {
    src: "data",
    loading: "loading",
  }),
  created() {
    this.$store.dispatch("about/fetchAbout");
  },
};
</script>

<style scoped lang="less">
.about-container {
  width: 100%;
  height: 100%;
  position: relative;
  .about-content {
    width: 100%;
    height: 100%;
  }
}
</style>
