<template>
  <div class="ImageLoader-container">
    <img v-if="!everysting" class="placeholder" :src="placeholder" alt="" />
    <img
      class="origin"
      :src="src"
      @load="handleLoaded"
      alt=""
      :style="{ opacity: orginOpacti, transition: `${duration}ms` }"
    />
  </div>
</template>

<script>
export default {
  data() {
    return {
      originLoaded: false,
      //  VUE 由数据主导 此处代表图片加载完成
      everysting: false,
      //   所有动作完成
    };
  },
  props: {
    src: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      required: true,
    },
    duration: {
      type: Number,
      default: 5000,
    },
  },
  computed: {
    orginOpacti() {
      return this.originLoaded ? 1 : 0;
    },
  },
  methods: {
    handleLoaded() {
      // console.log("加载完成");
      this.originLoaded = true;
      setTimeout(() => {
        this.everysting = true;
        this.$emit("load");
      }, this.duration);
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/css/mixin.less";
.ImageLoader-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  img {
    .self-fill();
    object-fit: cover;
  }
  .origin {
    opacity: 0;
  }
  .placeholder {
    filter: blur(2vw);
    // z-index: -99;
  }
}
</style>
