<template>
  <div class="Detail-container">
    <Layout>
      <template #main>
        <div class="main-container" v-vloading="isLoading" ref="scollcontainer">
          <Main :blog="data" v-if="data" />
          <BlogDetail v-if="!isLoading" />
        </div>
      </template>
      <template #right>
        <div class="nav-container" v-vloading="isLoading" v-if="data">
          <Nav :Toc="data.toc" />
        </div>
      </template>
    </Layout>
  </div>
</template>

<script>
import fetchData from "@/mixins/fetchData";
import Layout from "@/comopnents/Layout";
import Nav from "./comonents/nav";
import { getBlog } from "@/api/blog";
import Main from "./comonents/main";
import BlogDetail from "./comonents/Blogconment";
export default {
  mixins: [fetchData(null)],
  components: {
    Layout,
    Nav,
    Main,
    BlogDetail,
  },
  methods: {
    async fetchData() {
      return await getBlog(this.$route.id);
    },
    handleScroll() {
      this.$bus.$emit("mainScroll", this.$refs.scollcontainer);
    },
    setscroll(scrolltop) {
      this.$refs.scollcontainer.scrollTop = scrolltop;
    },
  },
  created() {
    this.$bus.$on("setscroll", this.setscroll);
  },
  mounted() {
    this.$bus.$emit("mainScroll", this.$refs.scollcontainer);
    this.$refs.scollcontainer.addEventListener("scroll", this.handleScroll);
  },

  updated() {
    const hash = location.hash;
    location.hash = "";
    setTimeout(() => {
      location.hash = hash;
    }, 50);
  },
  beforeDestroy() {
    this.$bus.$off("setscroll", this.setscroll);
    this.$bus.$emit("mainScroll");
    console.log(33333333);
    this.$refs.scollcontainer.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

<style scoped lang="less">
.Detail-container {
  height: 100%;
}
.main-container {
  overflow-y: scroll;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
  scroll-behavior: smooth;
}
.nav-container {
  width: 200px;
  position: relative;
  height: 100%;
  overflow-y: scroll;
}
</style>
