import Home from "@/views/Home";
import About from "@/views/About";
import Blog from "@/views/Blog";
import Message from "@/views/Message";
import Project from "@/views/Project";
import Detail from "@/views/Blog/compnents/Detail";

export default [
  { name: "home", path: "/", component: Home, meta: { title: "首页" } },
  {
    name: "about",
    path: "/about",
    component: About,
    meta: { title: "关于我" },
  },
  { name: "blog", path: "/article", component: Blog, meta: { title: "博客" } },
  {
    name: "CategoryBlog",
    path: "/article/cate/:categoryId",
    component: Blog,
    meta: { title: "文章" },
  },
  {
    name: "Detail",
    path: "/article/:id",
    component: Detail,
    meta: { title: "文章详情" },
  },
  {
    name: "message",
    path: "/message",
    component: Message,
    meta: { title: "留言板" },
  },
  {
    name: "project",
    path: "/project",
    component: Project,
    meta: { title: "项目" },
  },
];
