<template>
  <div
    class="Carouselitem-container"
    ref="container"
    @mousemove="handleMove"
    @mouseleave="handleleave"
  >
    <div class="carousel-container" :style="imgPosition" ref="inner">
      <ImageLoader
        :src="carouse.bigImg"
        :placeholder="carouse.midImg"
        @load="showWords"
      />
    </div>
    <div class="title" ref="title">{{ carouse.title }}</div>
    <div class="dies" ref="dies">{{ carouse.description }}</div>
  </div>
</template>

<script>
import ImageLoader from "@/comopnents/ImageLoader";
export default {
  props: ["carouse"],
  components: {
    ImageLoader,
  },
  data() {
    return {
      titleWidth: 0,
      diesWidth: 0,
      containerSize: null, //外层容器尺寸
      innerSize: null, //里面图片尺寸
      moseX: 0,
      moseY: 0,
    };
  },
  mounted() {
    this.titleWidth = this.$refs.title.clientWidth;
    this.diesWidth = this.$refs.dies.clientWidth;
    // this.showWords();

    this.setSize();
    //  获取窗口尺寸以及图片尺寸

    // 初始化鼠标保持中央
    this.moseX = this.centerclient.left;
    this.moseY = this.centerclient.top;
    window.addEventListener("resize", this.setSize);
  },
  destroyed() {
    window.removeEventListener("resize", this.setSize);
  },
  methods: {
    showWords() {
      // 标题
      this.$refs.title.style.opacity = 1;
      this.$refs.title.style.width = 0;
      //   强制渲染一次
      this.$refs.title.clientWidth;
      this.$refs.title.style.transition = "1s";
      this.$refs.title.style.width = this.titleWidth + "px";

      // 描述
      this.$refs.dies.style.opacity = 1;
      this.$refs.dies.style.width = 0;
      //   强制渲染一次
      this.$refs.dies.clientWidth;
      this.$refs.dies.style.transition = "2s 1s";
      this.$refs.dies.style.width = this.diesWidth + "px";
    },
    setSize() {
      this.containerSize = {
        width: this.$refs.container.clientWidth,
        height: this.$refs.container.clientHeight,
      };
      this.innerSize = {
        width: this.$refs.inner.clientWidth,
        height: this.$refs.inner.clientHeight,
      };
      // console.log(this.containerSize, this.innerSize);
    },
    handleMove(e) {
      // 鼠标相对于父级的位置
      // console.log(e);
      const rect = this.$refs.container.getBoundingClientRect();
      // console.log(rect);
      this.moseX = e.clientX - rect.left;
      this.moseY = e.clientY - rect.top;
      // console.log(this.moseY);
    },
    handleleave() {
      this.moseX = this.centerclient.left;
      this.moseY = this.centerclient.top;
    },
  },
  computed: {
    imgPosition() {
      if (!this.innerSize || !this.containerSize) {
        return {};
      }
      // 图片坐标
      const mostWidth = this.innerSize.width - this.containerSize.width;
      const mostHeight = this.innerSize.height - this.containerSize.height;
      // console.log(mostWidth, mostHeight);
      const left = -(mostWidth / this.containerSize.width) * this.moseX + "px";
      const top = -(mostHeight / this.containerSize.height) * this.moseY + "px";
      // console.log(this.containerSize.width, this.containerSize.height);
      return {
        transform: `translate(${left},${top})`,
      };
    },
    centerclient() {
      return {
        left: this.containerSize.width / 2,
        top: this.containerSize.height / 2,
      };
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/css/color.less";
.Carouselitem-container {
  // background: @dark;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  .carousel-container {
    width: 110%;
    height: 110%;
    position: absolute;
    left: 0;
    top: 0;
    transition: 0.3s;
  }
  .title,
  .dies {
    position: absolute;
    left: 30px;
    color: #fff;
    opacity: 0;
    white-space: nowrap;
    overflow: hidden;
    text-shadow: 1px 0px 0px rgba(0, 0, 0, 0.5), -1px 0px 0px rgba(0, 0, 0, 0.5),
      0px 1px 0px rgba(0, 0, 0, 0.5), 0px -1px 0px rgba(0, 0, 0, 0.5);
  }
  .title {
    top: calc(50% - 40px);
    font-size: 2em;
  }
  .dies {
    top: calc(50% + 30px);
    font-size: 1.2em;
    color: lighten(@gray, 20%);
  }
}
</style>
